.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  font-family: var(--font-family-primary);
  font-weight: var(--fw-400);
  letter-spacing: 0;
  background-color: var(--color-white);
  color: var(--color-brown-800);
  font-optical-sizing: auto;
  font-size: clamp(16px, 1vw, 20px);
  font-style: normal;
  line-height: 1;
}

.page__body {
  min-height: var(--vh);
  min-width: 320px;
}

.page.background {
  background-color: var(--color-blue-400);
}

.page.open {
  overflow: hidden;
}

.container {
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
  margin: 0 auto;
}

.site-container {
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.background {
  background-color: var(--color-white-150);
}

.logo {
  width: 100%;
  max-width: 232px;
  font-size: 24px;
}

.before--elem {
  position: relative;
}

.before--elem:before {
  content: "";
  background-color: var(--color-green-350);
  width: 12px;
  height: 12px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  width: 100%;
  height: 100dvh;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  font-weight: var(--fw-700);
  letter-spacing: 0;
  color: var(--color-brown-800);
  width: fit-content;
  line-height: 1.21;
  overflow: hidden;
}

h1 {
  color: var(--color-white);
  font-size: clamp(2.25rem, 1.9951rem + 1.0458vi, 60px);
  line-height: 1.1;
}

